.page-header__background-wrapper {
  background-color: #283645;
}
.page-header__logo-wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  /*временно для сетки*/
  height: 70px;
}
.page-header__logo-link {
  height: 40px;
  cursor: pointer;
}
.page-header__logo-mobile {
  position: absolute;
  top: 16px;
  left: 20px;
  fill: #ffffff;
}
.page-header__logo-mobile:hover {
  fill: #d22856;
}
.page-header__logo-mobile:focus {
  fill: #d22856;
}
.page-header__logo-mobile:active {
  fill: rgba(255, 255, 255, 0.3);
}
.page-header__toggle {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 25px;
  height: 26px;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: transparent;
  border-style: none;
  outline: none;
  cursor: pointer;
}
.page-header__toggle-icon {
  width: auto;
  height: auto;
  fill: #ffffff;
}
.page-header__toggle-icon:hover {
  fill: #d22856;
}
.page-header__toggle-icon:focus {
  fill: #d22856;
}
.page-header__toggle-icon:active {
  fill: rgba(210, 40, 86, 0.3);
}
.page-header__logo-desktop {
  display: none;
}
.page-header__logo-tablet {
  display: none;
}
