    right: 0;
    bottom: 35px;
    left: 40px;
    width: 70px;
    height: 3px;
    background-color: #ffffff;
  }
  .main-nav__index-active {
    position: relative;
  }
  .main-nav__index-active::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 35px;
    left: 40px;
    width: 70px;
    height: 3px;
    background-color: #ffffff;
  }
