  content: "";
  position: absolute;
  top: 10px;
  left: 10px;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: #d22856;
}
.user-application__list-input:checked ~ .user-application__list-checkbox-off {
  display: none;
}
.user-application__list-input:checked ~ .user-application__list-checkbox-on {
  display: block;
}
